
.platform-rmis,
.platform-highway,
.platform-mcp {
    display: flex;
    gap: 6px;
    align-items: center;
    &:after {
        content: '';
        display: block;
        width: 20px;
        height: 20px;
        background-repeat: no-repeat;
        background-size: contain;
    }
}

.platform-highway {
    &:after {
        background-image: url(data:image/png;base64,);
    }
}

.platform-rmis {
    &:after {
        background-image: url(data:image/png;base64,);
    }
}

.platform-mcp {
    &:after {
        background-image: url(data:image/png;base64,);
    }
}
